import { useEffect, useState, useRef } from "react"
import styled, { css } from 'styled-components'
import { useSelector } from 'react-redux'

const { kakao } = window;

const Map = () => {
    const [kakaoMap, setKakaoMap] = useState(null);
    const container = useRef(null);
    const storeList = useSelector((state)=>{ return state.storeList }) // redux store  

    const initMap = () => {
        const center = new kakao.maps.LatLng(37.570533, 126.992156); //  
        const options = {
            center,
            level: 3,
        };
        const map = new kakao.maps.Map(container.current, options);

        for(let i=0; i<storeList.length; i++) {
            // -   
            const geocoder = new kakao.maps.services.Geocoder();

            //   
            geocoder.addressSearch(storeList[i].storeAddress, function (result, status) {
                //    
                if (status === kakao.maps.services.Status.OK) {

                    const coords = new kakao.maps.LatLng(result[0].y, result[0].x);

                    //     
                    const marker = new kakao.maps.Marker({
                        map: map,
                        position: coords
                    });

                    //     
                    const infowindow = new kakao.maps.InfoWindow({
                        content: '<div style="width:150px;text-align:center;padding:6px 0;">'+storeList[i].storeName+'</div>'
                    });
                    
                    infowindow.open(map, marker);
                    //      
                    map.setCenter(coords);
                }
            });
        }
       
        setKakaoMap(map);
    };

    useEffect(() => {
        initMap();
    }, []);

    return <MapContainer id="KakaoMap" ref={container} />;
}

const MapContainer = styled.div`
    width: '100%',
    height : '626px'
  `;

export default Map